<template>
  <div
    class="wall"
    :class="{
      fade: selectedWall && !isSelected,
      ghost,
      selected: isSelected,
			positive,
			negative: !positive,
    }"
    :style="{
      'grid-column': column + 1,
      left: dragDistance + 'px',
      'margin-right': (-1 * resizeDistance) + 'px'
    }"
  >
    <div
      class="line"
      :class="{
				positive,
				negative: !positive,
				selected: isSelected,
			}"
      @mousedown="mousedownLabel"
      @mouseup="mouseupLabel"
    ></div>
    <div
      class="label"
      :class="{
        dragging,
        editing: isSelected && !dragging,
      }"
      ref="label"
      :contenteditable="isSelected && !dragging"
      @mousedown="mousedownLabel"
      @mouseup="mouseupLabel"
      @input="updateText"
      @keydown.enter="enterKey"
      @keydown.delete="deleteKey"
      v-html="displayLabel"
    ></div>

    <WallOptions
      :active="isSelected && !dragging"
      @delete="deleteWall"
    />
  </div>
</template>

<script>
import WallOptions from './WallOptions'

export default {
  components: { WallOptions, },
  props: {
    positive: {
      type: Boolean,
      default: true,
    },
    column: {
      type: Number,
      default: 0,
    },
    label: {
      type: String,
      default: '',
    },
    id: {
      type: Number,
      required: true,
    },
    ghost: {
      type: Boolean,
      default: false,
    },
  },
  data () {
    return {
      dragging: false,
      displayLabel: this.label,
    }
  },
  computed: {
    selectedBar () { return this.$store.state.selectedBar },
		selectedWall () { return this.$store.state.selectedWall },
    isSelected () { return this.selectedWall === this.id },
    trackingMouseXForDrag () { return this.$store.state.trackingMouseXFor === 'drag' },
    dragDistance () {
      if (!this.dragging || this.$store.state.trackingMouseXFor !== 'drag') return 0
      return this.$store.state.mouseXDifference
    },
    resizeDistance () {
      if (!this.resizing || this.$store.state.trackingMouseXFor !== 'resize') return 0
      return this.$store.state.mouseXDifference
    },
  },
  watch: {
    trackingMouseXForDrag (isTracking) {
      if (!isTracking && this.isSelected) {
        this.mouseupLabel()
      }
    },
    isSelected (isSelected) {
      if (!isSelected && this.$refs.label.innerHTML === '')
        this.$store.commit('deleteWall', this.id)
      if (isSelected && !this.dragging && !this.resizing)
        this.$nextTick(() => this.selectText())
    }
  },
  methods: {
    updateText (event) {
      const textToSave = event.target.innerText
      this.$store.commit('updateWall', {
        id: this.id,
        label: textToSave
      })
    },
    enterKey (event) {
      event.preventDefault()
      return this.$store.commit('selectWall')
    },
    deleteKey (event) {
      if (event.srcElement.innerHTML === '') {
        this.$store.commit('deleteWall', this.id)
        this.$store.commit('selectWall')
      }
    },
    mousedownLabel (event) {
      if (this.isSelected)
        return
      this.$store.commit('selectWall', this.id)
      this.dragging = true
      this.$store.commit('setTrackMouseX', {
        for: 'drag',
        mouseXStart: event.clientX,
      })
    },
    mouseupLabel () {
      if (this.dragging === false) return
      this.dragging = false
      const mapWidth = this.$store.state.mapWidth
      const shiftAsPercent = Math.round((this.$store.state.mouseXDifference / mapWidth) * 100)
      if (shiftAsPercent === 0) {
        this.$nextTick(() => this.selectText())
      }
      else {
        const toUpdate = {
          id: this.id,
          column: this.column + shiftAsPercent,
        }
        if (this.column + shiftAsPercent > 100)
          toUpdate.column = 100
        if (this.column + shiftAsPercent < 0)
          toUpdate.column = 0
        this.$store.commit('updateWall', toUpdate)
        this.$store.commit('selectWall')
      }

      this.$store.commit('setTrackMouseX')
    },
    selectText () {
      const labelElement = this.$el.querySelector('.label')
      labelElement.focus()
      if (document.body.createTextRange) {
        const range = document.body.createTextRange()
        range.moveToElementText(labelElement)
        range.select()
      }
      else if (window.getSelection) {
        const selection = window.getSelection()
        const range = document.createRange()
        range.selectNodeContents(labelElement)
        selection.removeAllRanges()
        selection.addRange(range)
      }
    },
    deleteWall () {
      this.$store.commit('deleteWall', this.id)
      this.$store.commit('selectWall')
    },
  }
}
</script>

<style lang="scss" scoped>

.wall {
  position: relative;
  padding: 2px;
  transition: box-shadow .2s, opacity .2s;
  z-index: 10;
  height: 100%;
  cursor: pointer;
	grid-row: -2;

  &.selected {
    z-index: 15;
    -webkit-transform:translate3d(180,0,0);
  }

	&.positive {
		color: #00d0a4;
	}

	&.negative {
		color: #f17b5f;
	}

  :focus {
    outline: 0;
  }

  &.fade {
    opacity: .3;
    z-index: 9;
  }

  &.ghost {
    opacity: .3;
    pointer-events: none;
  }

  &:hover {
    .resizer { opacity: 1; }
  }

  .line {
    position: absolute;
    top: 0;
    left: 0;
    width: 8px;
		height: 400%;
    z-index: 1;
    // opacity: .8;

    &.positive {
      background-image: linear-gradient(to top, rgba(#00d0a4, 0) 0%, rgba(#00d0a4, .4) 99.5%);
    }

    &.negative {
      background-image: linear-gradient(to bottom, rgba(#f17b5f, 0) 0%, rgba(#f17b5f, .4) 99.5%);
      top: -300%;
    }

    &.selected {
      z-index: 14 !important;
      opacity: 1;
    }
  }

  .label {
    position: relative;
		left: 12px;
    top: 6px;
    font-size: .9em;
    font-weight: 400;
    user-select: none;
    width: 80px;
    overflow: hidden;
    z-index: 2;
    cursor: pointer;
    user-select: none;
    line-height: 1.1;

    &.dragging {
      cursor: grab;
    }
    &.editing {
      cursor: text;
      user-select: auto;
    }

    &::selection {
      background: rgba(#000, .1);
    }
  }
}



</style>



