<template>
  <div
    class="half"
    :class="{ top }"
    @mouseover="mouseover"
    @mouseout="mouseout"
    @mousemove="mouseover"
    @click="click"
  >
    <Bar
      v-for="bar in shownBars"
      :key="bar.id"
      v-bind="bar"
    />
    <Wall
      v-for="wall in shownWalls"
      :key="wall.id"
      v-bind="wall"
    />
    
  </div>
</template>

<script>
import Bar from './Bar'
import Wall from './Wall'

export default {
  components: { Bar, Wall },
  props: {
    bars: {
      type: Array,
      default: () => [],
    },
    walls: {
      type: Array,
      default: () => [],
    },
    top: {
      type: Boolean,
      default: true,
    }
  },
  data () {
    return {
      shownBars: this.bars,
      shownWalls: this.walls,
      hovering: false,
      ghostWall: false,
      ghostBar: false,
      wallMouseCutoff: 240,
    }
  },
  computed: {
    selectedBar () { return this.$store.state.selectedBar },
    selectedWall () { return this.$store.state.selectedWall },
    mapWidth () { return this.$store.state.mapWidth },
  },
  watch: {
    bars (newBars) {
      this.shownBars = newBars
    },
    walls (newWalls) {
      this.shownWalls = newWalls
    }
  },
  methods: {
    spawnGhostWall () {
      this.ghostWall = true
      if (this.ghostBar)
        this.killGhosts()
      this.shownWalls.push({
        column: this.getNewWallPosition(event.clientX - this.$el.offsetLeft),
        id: 99999,
        positive: this.top,
        label: `New Wall`,
        ghost: true,
      })
    },
    spawnGhostBar () {
      this.ghostBar = true
      if (this.ghostWall)
        this.killGhosts()
      this.shownBars.push({
        start: this.getNewBarPosition(event.clientX - this.$el.offsetLeft),
        length: 12,
        id: 99999,
        positive: this.top,
        label: `New ${ this.top ? '' : 'Anti-' }Value`,
        ghost: true,
      })
    },
    killGhosts () {
      if (this.ghostBar) {
        this.ghostBar = false
        this.shownBars.pop()
      }
      if (this.ghostWall) {
        this.ghostWall = false
        this.shownWalls.pop()
      }
    },
    updateGhost (type) {
      if (type === 'bar') {
        if (!this.ghostBar) {
          this.killGhosts()
          this.spawnGhostBar()
        }
        else {
          this.$set(
            this.shownBars[this.shownBars.length - 1], 
            'start', 
            this.getNewBarPosition(event.clientX - this.$el.offsetLeft)
          )
        }
      }
      else if (type === 'wall') {
        if (!this.ghostWall) {
          this.killGhosts()
          this.spawnGhostWall()
        }
        else {
          this.$set(
            this.shownWalls[this.shownWalls.length - 1], 
            'column', 
            this.getNewWallPosition(event.clientX - this.$el.offsetLeft)
          )
        }
      }
    },
    spawnReal (type) {
      this.killGhosts()
      if (type === 'wall') {
        this.$store.commit('addWall', {
          column: this.getNewWallPosition(event.clientX - this.$el.offsetLeft),
          positive: this.top,
          label: `New Wall`,
        })
        this.$nextTick(() => {
          this.$nextTick(() => this.$store.commit('selectWall', this.$store.state.lastAddedWallId))
        })
      }
      else if (type === 'bar') {
        this.$store.commit('addBar', {
          start: this.getNewBarPosition(event.clientX - this.$el.offsetLeft),
          length: 12,
          positive: this.top,
          label: `New ${ this.top ? '' : 'Anti-' }Value`,
        })
        this.$nextTick(() => {
          this.$nextTick(() => this.$store.commit('selectBar', this.$store.state.lastAddedBarId))
        })
      }
    },

    mouseover (event) {
      if ((event.target ? event.target : event.path[0]).classList.contains('half') && !this.selectedBar && !this.selectedWall) {
        this.hovering = true
        const ghostToShow = event.offsetY > this.wallMouseCutoff ? 'wall' : 'bar'
        this.updateGhost(ghostToShow)
      }
    },
    mouseout (event) {
      if ((event.target ? event.target : event.path[0]).classList.contains('half') && !this.selectedBar && !this.selectedWall) {
        this.killGhosts()
      }
    },
    click (event) {
      if ((event.target ? event.target : event.path[0]).classList.contains('half') && !this.selectedWall && !this.selectedBar && !this.$store.state.trackingMouseXFor) {
        const ghostToCreate = event.offsetY > this.wallMouseCutoff ? 'wall' : 'bar'
        this.spawnReal(ghostToCreate)
      }
    },
    getNewBarPosition (mouseX) {
      let centeredX = mouseX - ((this.mapWidth / 100) * 6)
      let start = Math.round((centeredX / this.mapWidth) * 100)
      if ( start < 0) start = 0
      if (start > 88) start = 88
      return start
    },
    getNewWallPosition (mouseX) {
      return Math.round((mouseX / this.mapWidth) * 100)
    }
  }
}
</script>

<style lang="scss" scoped>

$rows: 8;
$rowHeight: 37px;

.half {
  width: 100%;
  display: grid;
  grid-auto-flow: row dense;
  align-items: start;
  grid-template-columns: repeat(100, 1%);
  grid-template-rows: repeat($rows, $rowHeight);
  height: $rows * $rowHeight;
  padding-bottom: $rows * $rowHeight;
  cursor: cell;
  z-index: 10;

  &.top {
    transform: rotateX(180deg);

    & > * {
      transform: rotateX(180deg);
    }
  }

  
}

</style>



